* {
	padding: 0;
	margin: 0;
	border: 0;
}
*,
*:before,
*:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
:focus,
:active {
	outline: none;
}
a:focus,
a:active {
	outline: none;
}

nav,
footer,
header,
aside {
	display: block;
}

html,
body,
#root {
	height: 100%;
	width: 100%;
	line-height: 1;
}
input,
button,
textarea {
	font-family: inherit;
}

input::-ms-clear {
	display: none;
}
button {
	cursor: pointer;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
a,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	vertical-align: top;
}
.container {
	margin: 0 auto;
	padding: 30px 15px;
	max-width: 1030px;
}

.home {
	form {
		margin-bottom: 20px;
		display: flex;
		column-gap: 10px;
		input {
			padding: 10px;
			width: 200px;
			height: 50px;
			border-radius: 10px;
			border: 1px solid #999;
		}
		button {
			padding: 0 10px;
			border-radius: 10px;
			&:hover {
				background-color: #999;
			}
		}
	}
}
